<template>
  <div class="relative">
    <button @click="isOpen = !isOpen" class="relative z-10 block h-6 w-6 rounded-full overflow-hidden border-2 border-gray-600 focus:outline-none focus:border-white">
      <img class="h-4 w-6 object-cover" src="@/assets/imgs/svgs/user-white.svg" alt="user">
    </button>
    <button v-if="isOpen" @click="isOpen = false" tabindex="-1" class="fixed inset-0 h-full w-full cursor-default"></button>
    
    <div v-if="isOpen" class="absolute mt-5 py-2 w-48 bg-white rounded-lg shadow-xl">
      <a href="#" class="block px-4 py-2 text-gray-800 hover:bg-indigo-500 hover:text-white">{{directive}}</a>
      <a href="#" class="block px-4 py-2 text-gray-800 hover:bg-indigo-500 hover:text-white">Support</a>
      <a href="#" class="block px-4 py-2 text-gray-800 hover:bg-indigo-500 hover:text-white">Sign out</a>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
    props: {
      directive: {
        type: String,
        required: true
      }

    },
  data() {
    return {
      isOpen: false
    }
  },
  computed: mapState({          
    project: state => state.project.project
  }),
  created() {
    const handleEscape = (e) => {
      if (e.key === 'Esc' || e.key === 'Escape') {
        this.isOpen = false
      }
    }

    document.addEventListener('keydown', handleEscape)

    this.$once('hook:beforeDestroy', () => {
      document.removeEventListener('keydown', handleEscape)
    })
  }
}
</script>

