<template>  
    <div id="nav" class="flex flex-row bg-gray-700 text-sm text-gray-500 font-light leading-normal items-center py-2 px-5 fixed-top" v-if="user.username">
      <ul class="flex">
        <li><img src="../../../public/img/icons/favicon.svg" class="w-8 h-8"></li>
        <li class="ml-auto py-1 px-2 hover:bg-gray-600 hover:text-gray-200 rounded" v-if="user.username"><router-link :to="'/userhome/' + user.uid">Home</router-link></li>
      <li class="ml-auto py-1 px-2 hover:bg-gray-600 hover:text-gray-200 rounded"><router-link to="/about">About</router-link></li>
     <li class="ml-auto py-1 px-2 hover:bg-gray-600 hover:text-gray-200 rounded"> <router-link to="/uploadfile">Upload File</router-link></li-->
     <li class="ml-auto py-1 px-2 hover:bg-gray-600 hover:text-gray-200 rounded"> <router-link to="/analist">Statistics</router-link></li>
      <!--li class="ml-auto py-1 px-2 hover:bg-gray-600 hover:text-gray-200 rounded"> <router-link to="/currentboq">Current BQ</router-link></li-->
      <li class="ml-auto py-1 px-2 hover:bg-gray-600 hover:text-gray-200 rounded" v-if="user.role.admin"> <router-link to="/project">Projects</router-link></li>
     
      </ul>
      <ul class="flex absolute right-0 items-center">
        <li class="ml-2 py-1 px-2 hover:bg-gray-600 hover:text-gray-200 rounded"> <router-link to="/settings">Settings</router-link></li>
       
        <li class="ml-2 py-1 px-2 hover:bg-gray-600 hover:text-gray-200 rounded"> <router-link to="/appendix">Appendix</router-link></li>
       
        <li class="ml-2 py-1 px-2 bg-gray-600 hover:text-gray-200 rounded"><watsCom /></li>
        <li class="ml-2 py-1 px-2 hover:bg-gray-600 hover:text-gray-200 cursor-pointer rounded" @click="logOut" > Logout</li>
       
        <li class="ml-2 py-1 px-2 text-gray-200 cursor-pointer flex flex-row"><userDropdown directive='user' /> <span class="font-semibold ml-2">{{user.username}}</span></li>
      </ul>
     
    </div>

</template>
<script>

import { mapState } from 'vuex';
import watsCom from '@/components/coms/watsapp.vue'
import fadeOut from '@/components/utils/fadeOut.vue';
import userDropdown from '@/components/ui/userDropDown.vue'

export default {
    name: "main-nav",
    components: {
      watsCom,
      fadeOut,
      userDropdown

    },
    data() {
      return {
        ui: {
                login: true,
                message: null,
                success: null,
                warn: true,
                fade: {
                    slow: 20000,
                    fast: 10000,
                    faster: 5000,
                    fastest: 2500
                }
            }
      }
      

    },    
    computed: mapState({ user: state => state.user.user }),  // Object
    methods: {
      logOut() {
        this.$store.dispatch('resetSession', this.user.username) // Handle cookie Destruction on server and client
        this.$store.dispatch('resetUser') // Log out of the client
        
        this.$router.push('/')
      }

    }

}
</script>