//import PouchDB from 'pouchdb';
import axios from 'axios';


//const db = new PouchDB('wkm_projects')
const URL = 'http://' + location.hostname + ':5984/wkm_projects/'
    
const design = {
    project_index: {
        design: "project-index",
        views: {
            table: "project-table",
            transactions: "transaction-index",
            user_projects: "user-projects"
        }
    },
    project_estimates: {
        design: "project-estimates",
        views: {
            update: "update-estimate",
            update_section: "update-section"
        }
    },
    project_profile: {
        design: "project-profile",
        views: {
            estimate: "estimate-view"
        }
    }

}

export default {
    state: {
        alpha_index: ['a','b','c','d','e','f','g','h','i','j','k','l','m','n','o','p','q','r','s','t','u','v','w','x','y','z'],
        project: {      // Current Project Data Container (Object)
            count: 0,
            data: {},   // Current Project Container (Object)
            estimates: [],
            currentEstimatePhases: new Set,
            currentEstimateSections: new Set,

            tables: {   // Sorted Project Tables Data (Object)
                levels: [], // Project floor data
                transactions: {
                    records: [],
                    incomes: [],
                    expences: []
                },
                estimates: {
                    index: [], // [{project.id, estimate.id}] 
                    estimate: {}
                },
                press: {
                    index: [],
                },
                documents: {
                    index: []
                }

            },
            charts: {  // Sorted Project Charts Data (Object)
                income: {  // total income of each project
                    amounts: [], 
                    dates: []  // median date 
                    
                },
                expence: {   // ditto ... expence 
                    amounts: [], 
                    dates: [] 
                    
                },
                earning: {
                    amounts: [], // ditto ... earning 
                    dates: [] 
                } 
                    
            },
            forms: {
                create: {} // new project form
                
            }   // Project Input Data (Object)
        },
        projects: {     // All Projects Sorted Data Container (Object)
            party: {
                index: [],   // All Projects Index Data Container [{project.id, project.name},] (Array)
                count: 0,
                tables: { 
                    index: [], // [{id, name, address, status, created, updated, author},]
                    transactions: {
                        index: [], // [{pid, transaction}]
                        incomes: [],
                        expences: []
                    },
                    estimates: {
                        index: [] // [{project.id, estimate.id}] 
                    }
                },               
                charts: {
                    income: {  // total income of each project
                        amounts: [], 
                        dates: [],  // median date 
                        pids: [] // project Id's
                    },
                    expence: {   // ditto ... expence 
                        amounts: [], 
                        dates: [],  
                        pids: [] 
                    },
                    earning: {
                        amounts: [], // ditto ... earning 
                        dates: [],  
                        pids: [] 
                    },
                    stats: {
                        active:{
                            projects: [], //
                            count: 0
                        },
                        completed:{
                            projects: [], //
                            count: 0
                        },
                        paused:{
                            projects: [], //
                            count: 0
                        }
                    }
                }
            },
            conflicting: {    // Ditto above ... Conflicting Projects
            index: [],   
            count: 0
            }
        },
    },
    mutations: {
        // ADDS
        ADD_ESTIMATE_ITEM(state, data) {
            if(state.project.currentEstimatePhases.has(data.phase) && state.project.currentEstimateSections.has(data.section)){  // Updating existing record
               
                var bq;
                state.project.estimates.forEach(bq => {
                    if(bq.phase == data.phase && bq.section == data.section){
                        state.count = bq.items.length
                        data.item.item_no = state.alpha_index[state.count]
                        bq.items.push(data.item)
                    }                            
                })
            }
            else {
                var items = []
                
                data.item.item_no = state.alpha_index[state.count]
                items.push(data.item)
                data.items = items
                data.comment = "state created data"
                data.meta_data = {
                  created: Date.parse(Date()),
                  updated: "",
                  author: "state",
                  updated_by: ""
                }
                delete(data.item)
                state.project.estimates.push(data)
            }
            //let url = URL + '_design/project-estimates/_update/estimates/' + state.project.data._id;
           /* axios.post(url, data).then(response => {
                console.log('UPDATE ESTIMATE RESPONSE', response)
            }).catch(error => {
                console.log(error)
            })*/
            //console.log('EESTIMATES',state.project.estimates)
            //console.log('DATA', state.project.data)
            //console.log('URL', url)
        },
       
        ADD_CURRENT_ESTIMATE_SECTION(state, section) {
            state.project.currentEstimateSections.add(section)
        },
        //SETS
        SET_PROJECT(state, project) {
            state.project.data = project
        },
        SET_PROJECTS(state, data) {
            //console.log('MUTATION SET-PROJECTS', projects)
            /* state.projects = projects  depricated */
            state.projects.party.index = data.rows
            state.projects.party.count = data.total_rows 
        },
        SET_PROJECTS_TABLE(state, data) {
            //console.log('MUTATION SET-PROJECTS Table', data)
            state.projects.party.tables.index = data.rows
        },
        SET_PROJECTS_TRANSACTIONS(state, data) {
            //console.log('MUTATION SET-PROJECTS Table', data)
            state.projects.party.tables.transactions.index = data.rows
        },
        SET_PROJECT_ESTIMATES(state, estimates){
            state.project.estimates = estimates 
            estimates.forEach(estimate => {
                state.project.currentEstimatePhases.add(estimate.phase)
                state.project.currentEstimateSections.add(estimate.section)
                
            })
            //console.log('setProjectEstimates', state.project.estimates)
                        
        },
        SET_CONFLICTS(state, conflicted) {
            state.conflicting.index = conflicted.rows
            state.conflicting.count = conflicted.total_rows
        },

        // RESETS
        RESET_PROJECT(state) {
            state.project.data = {}
        },
        // UPDATES
      
    },
    actions: {
        // ADDS
        async addEstimateItem(context, data){
            context.commit('ADD_ESTIMATE_ITEM', data)
        },
        async addCurrentEstimateSections(context, section){
            context.commit('ADD_CURRENT_ESTIMATE_SECTION', section)
        },
        // GETS
        async getProjects(context){
            
            const url = URL + '_all_docs',//'?include_docs=true'
                alldocs = URL + '_all_docs?include_docs=true',
                index = URL + '_design/project-index/_view/user-projects';
            axios.get(index).then(response => {
                //console.log(response)
                context.commit('SET_PROJECTS', response.data)
                
            }).catch(error => {
                console.log(error)
            })

        },
        async getConflictedProjects(context){
            
            const url = URL + 'conflict_test/_all_docs?include_docs=true&conflicts=true'
            axios.get(url).then(response => {
                //console.log(response)
                context.commit('SET_CONFLICTS', response.data)
                
            }).catch(error => {
                console.log(error)
            })

        },
        async getProject(context, id){  
            context.commit('RESET_PROJECT')          
            const url = URL + id + '?include_docs=true&attatchments=true';               
            axios.get(url).then(response => {
                //console.log('PROJECT ACTION', response)
                context.commit('SET_PROJECT', response.data)  
                context.commit('SET_PROJECT_ESTIMATES', response.data.estimates)
                
            }).catch(error => {
                console.log(error)
            })

        },
        async getProjectsTable(context){
            
            const index = URL + '_design/project-index/_view/project-table';
            axios.get(index).then(response => {
                //console.log('PROJECT TABLE', response)
                context.commit('SET_PROJECTS_TABLE', response.data)
                
            }).catch(error => {
                console.log(error)
            })
        },
        async getProjectsTransactions(context){            
            const index = URL + '_design/project-index/_view/transaction-index';
            axios.get(index).then(response => {
                //console.log('TRANSACTIONS',response)
                context.commit('SET_PROJECTS_TRANSACTIONS', response.data)
                
            }).catch(error => {
                console.log(error)
            })

        }
        // UPDATES
    },
    getters: {
        // INDEXES
        alpha_index: state => state.alpha_index,
        // ESTIMATES
        estimates: state => state.project.estimates,
        currentEstimatePhases: state => state.project.currentEstimatePhases,
        currentEstimateSections: state => state.project.currentEstimateSections,
        // PROJECT
        project: state => state.project.data,
        projects: state => state.projects.party.index,
        projectscount: state => state.projects.parity.count,
        // conflicted projects
        conflicts: state => state.conflicting.index,
        conflictscount: state => state.conflicting.count,
        // TABLES DATA
        pit: state => {
            var data = [];
            state.projects.party.tables.index.forEach(item => {
                data.push(item.key)
            })
            return data
        }, // Projects index table
        transactions: state => {
            return state.projects.party.tables.transactions.index
        }

    }

}