const axios = require ('axios'),
    URL = 'http://' + location.hostname + ':5984/wkm_sessions/';

export default {
    state: {
        session: {},
        user: {}

    },
    mutations: {
        LOAD_SESSION(state, data) {
            state.session = data
        },
        LOAD_USER(state, data) {
            state.user = data
        }
    },
    actions: {
        async loadSession(context, username){
            var url = URL + username
            return new Promise(
                function (resolve, reject) {
                
                    axios.get(url)
                    .then(response => {
                        context.commit('LOAD_SESSION', response.data.session)
                        context.commit('LOAD_USER', response.data.session.cookie.metadata.user) 
                        //console.log('BIG S',response.data.session._id)
                        resolve(response.data.session._id)
                    })
                    .catch(error => {
                        console.log(error)
                        reject(error.message)
                    })
                }
            )
            

        },
        async getUserLocation(context){
            var locationData = {
                message: null,
                lat: null,
                long: null
            }
            if (navigator.geolocation) {
                navigator.geolocation.getCurrentPosition(showPosition)
            } else {
                locationData.message = "Geolocation is not Available"
            }
            function showPosition(position) {
                locationData.lat = position.coords.latitude;
                locationData.long = position.coords.longitude;
                locationData.message = "Geolocation Available"
            }

        }
    },
    getters: {
        user: state => state.user,
        session: state => state.session
    }
}