import { ArrayModel, BasicModel, Model, ObjectModel } from "objectmodel";

Model.prototype.conventionForPrivate = key => key.startsWith('#');

export const Location = ObjectModel({
    lat_x: [Number],
    lat_y: [Number]
})

export const Address = new ObjectModel({
    lot: [String],
    street: [String],
    town: [String],
    city_parish: [String],
    country: [String],
    location: [Location]
}).defaultTo({
    country: "Jamaica W.I."
})

const Contact = new ObjectModel({
    email: [String],
    tel: [Number],
    mobile: [Number]
})

const Legal = new ObjectModel({
    vol: String,
    fol: String,
    dwgno: [String]
})

const Level = new ObjectModel({
    level: String,
    area: Number,
    unit: String

})

const Note = new ObjectModel({
    start: [Number],
    end: [Number],
    title: [String]

})

const Client = new ObjectModel({
    name: String, // madatory
    address: [Address], // optional
    contact: [Contact],
    showForm: Boolean

})

const Status = new ObjectModel({
    active: Boolean,
    completed: Boolean,
    paused: Boolean,
    terminated: Boolean
})

const Meta_data = new ObjectModel({
    author: String,
    created: Number,
    updated: Number,
    updatedby: String,
    
})

const Collab = new ObjectModel({
    collaborator: String,
    start: Date,
    end: Date

})

const Deposit = new ObjectModel({
    id: String,
    amount: Number,
    date: Date,
    method: String, // payment method
    payee: String,
    refference: [Number, String], // either number or string
    type: String
}).defaultTo({
    type: "deposit"
})

const Withdraw = new ObjectModel({
    id: String,
    amount: Number,
    date: Date,
    method: String, // payment method
    paidto: String,
    refference: [Number, String], // either number or string
    type: String
}).defaultTo({
    type: "withdraw"
})

const Document = new ObjectModel({
    id: String,
    title: String,
    notes: String,
    sheet_no: [Number],
    url: String,
    doc_type: String,
    filename: String,
    metadata: ObjectModel({
        uploaded: Date,
        uploaded_by: String
    })

})

const Presentation = new ObjectModel({
    id: String,
    section: String,
    img: String,
    index: String,
    title: String,
    text: String,

})

const Account = new ObjectModel({
    started: Date,
    updated: [Date],
    ballance: Number, 
    records: [ArrayModel([Deposit, Withdraw])]
})

const Role = new ObjectModel({
    admin: [Boolean],
    user: [Boolean],
    staff: [Boolean],
    guest: [Boolean]
}).defaultTo({
    admin: false,
    user: true,
    staff: false,
    guest: false

})

const UserStatus = new ObjectModel({
    active: [Boolean],
    confirmed: [Boolean],
    terminated: [Boolean]
}).defaultTo({
    active: true,
    confirmed: false,
    terminated: false
})

const Name = ObjectModel({
    firstname: [String],
    lastname: [String],
    username: [String]
  })

const Personal = ObjectModel({
    dob: Date,
    name: [Name]
})

export class User extends Model({
    _id: [String],
    username: [String],
    password_hash: [String],
    personal: [Personal],
    address: [Address],
    contact: [Contact],
    department: [String],
    status: [UserStatus],
    role : [Role],
    meta: [Meta_data]
}){
    get fullName(){return  `${this.personal.firstname} ${this.personal.lastname}`; }
}


    