<template>
    <div class="flex flex-row">
        <input class="text-gray-700 py-1 px-1 mx-1 rounded-lg" type="text" placeholder="wattsapp contact" v-model.lazy="contact">  
         <input class="text-gray-700 py-1 px-1 mx-1 rounded-lg" type="text" placeholder="message" v-model.lazy="message"> 
         <!--button @click="setMessage"><img class="w-8 h-8 px-1 items-center" src="@/assets/imgs/svgs/comment-white.svg" /></button-->      
    <div id="watscom"></div>
    </div>
</template>

<script>
//import wasap from 'wasap.js'

export default {
    name: "wats-com",
    data() {
        return {
            contact: null,
            message: null,
            payload: null
        }

    },
    watch: {
        message: 'setMessage'
    },

    methods: {
        async setMessage() {
            var watsapp = document.getElementById('watscom');
            if(this.contact && this.message){
                if(this.contact.length < 11 || this.contact.length > 11){
                    this.contact = 'Invalid Watsapp Contact'
                    setTimeout(() => {
                        this.contact = null
                    }, 3500);                    

                }
                var aTag = document.createElement('a'),
                    img = document.createElement('img');
                    
                    //img.setAttribute('src', "../assets/imgs/svgs/comment-white.svg")
                    aTag.setAttribute('class', "px-2 py-1 items-center bg-white text-gray-700 font-semibold rounded-lg")

                    aTag.setAttribute('href',"https://wa.me/" + this.contact + "?text=" + this.message);
                    aTag.setAttribute('target', "_blank")
                    aTag.innerText = "send";
                    //aTag.appendChild(img);
                    watsapp.appendChild(aTag);
                
                               

            }else {
                this.contact = 'Fill in your contact info'
                this.message = 'Empty Message'
                await this.resetMessage('all')
               
            }
        },
        async resetMessage(flag=null) {
            
             setTimeout(() => {
                 if(flag == 'all'){

                    this.contact = null
                    this.message = null
                 }else {
                     this.message = null
                 }
                }, 4000);
        }

    }
}
</script>