var URL = 'http://' + location.hostname + ':5984/labour_rates/';
const axios = require ('axios')
export default {

    state: {
        dayrates: [],
        jobrates: []
    },
    mutations: {
        LOAD_JOBRATES(state, data){
            state.jobrates = data
            //alert('JOB RATES LOADED')
        },
        ADD_JOB_RATE(state, rate) {
            state.jobrates.push(rate)
        }
    },
    actions: {
        async loadJobRates(context) {
            var url = URL + '_all_docs?include_docs=true',
            
            index = URL + '_design/jobRates/_view/rate-index';

            axios.get(index)
            .then(result => {
                //console.log(result.data.rows)
                context.commit('LOAD_JOBRATES', result.data.rows )

            })
            .catch(error => {
                console.log(error)
            })


        },
        async addJobRate(context, rate){
            context.commit('ADD_JOB_RATE', rate)
            axios.put(URL + rate._id, rate)
            .then(response => {
                //console.log(response)
            })
            .catch(error => {
                console.log(error)
            })
        }
    },
    getters: {
        jobrates: state => state.jobrates
    }
  
} 